body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.jumbotron {
  text-align: center;
  height: 25vh;
}

.card-area {
  width: 50%;
  max-width: 50%;
  margin: auto;
}

.card-columns {
  -webkit-column-count:4;
  column-count:4;
}

.card {
  height: 17vh;
}

.card-img {
  height: 100%;
  width: 100%;
  background-size: cover;
}

.score-container {
  max-width: 50%;
  margin: auto;
  padding-bottom: 2em;
}

.score-right {
  text-align: right;
}

.col {
  padding: 0 !important;
}

.game-msg {
  text-align: center;
  position: absolute;
  right: 0%;
  z-index: 9999;
}

.message--flash {
  -webkit-animation: blink .5s ease-out;
          animation: blink .5s ease-out;
}

@-webkit-keyframes blink {
	0% {opacity: 0;}
	49%{opacity: 0;}
  50% {opacity: 1;}
}

@keyframes blink {
	0% {opacity: 0;}
	49%{opacity: 0;}
  50% {opacity: 1;}
}




.correct {
  color: green;
}

.incorrect {
  color: red;
}

.correct, .incorrect {
  text-transform: capitalize;
}
